#image1{
  width: 735.4px;
    height: 372px;
    margin-right: -26px;
  padding: 0px;
  border-radius: inherit;
  object-fit: cover;
  -webkit-transform: none;
          transform: none;
    opacity: 1;
    -webkit-filter: brightness(0.95);
            filter: brightness(0.95);
  transition: opacity 1.63s ease 0s, -webkit-transform 0.4s ease-in-out 0s, -webkit-filter 0.4s ease 0s;
  transition: transform 0.4s ease-in-out 0s, opacity 1.63s ease 0s, filter 0.4s ease 0s;
  transition: transform 0.4s ease-in-out 0s, opacity 1.63s ease 0s, filter 0.4s ease 0s, -webkit-transform 0.4s ease-in-out 0s, -webkit-filter 0.4s ease 0s;
}

.image1cont{
  width: 735.4px;
  height: 372px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  overflow: hidden;
  position: relative;
}

.image2cont{
  width: 186.8px;
  height: 182px;
  margin: 0px;
  padding: 0px;
  overflow: hidden;
  position: relative;
}

.image2{
  width: 186.8px;
  height: 182px;
  /* width: 100%;
  height: 100%; */
  margin: 0px;
  padding: 0px;
  -webkit-filter: brightness(0.95);
          filter: brightness(0.95);
  -webkit-transform: none;
          transform: none;
    opacity: 1;
  transition: opacity 1.63s ease 0s, -webkit-transform 0.4s ease-in-out 0s, -webkit-filter 0.4s ease 0s;
  transition: transform 0.4s ease-in-out 0s, opacity 1.63s ease 0s, filter 0.4s ease 0s;
  transition: transform 0.4s ease-in-out 0s, opacity 1.63s ease 0s, filter 0.4s ease 0s, -webkit-transform 0.4s ease-in-out 0s, -webkit-filter 0.4s ease 0s;
}

.image2:hover, #image1:hover{
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  cursor: pointer;
  -webkit-filter: brightness(1.05);
          filter: brightness(1.05);
}

.RestClckbdy{
  font-family: 'Montserrat', sans-serif;

}

.RestClckbtn li{
  display:inline;
  list-style-type: none;
  border: 1px solid #9c9c9c;
  padding: 8px 7px 8px 7px;
  border-radius: 7px;
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  cursor: pointer;
  color: #1c1c1c;
  margin-right: 20px;
}

.RestClckbtn li:hover{
background-color: rgb(248, 248, 248);
}

.RestClckdetails{
padding-left: 17px;
}

.RestClcktitle{
  font-size: 36px;
  font-weight: bold;
}

.delCount{
  font-weight: bold;
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
}

.delRev{
  font-family: 'Montserrat', sans-serif;
  font-size: 11px;
  padding: 1px;
  border-bottom: 1px dashed black;
}

.delRating{
  background-color: #38882f;
  color: white;
  font-size: 16px;
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  border-radius: 0.3rem;
  width: 52px;
    height: 26px;
  display: flex;
  flex-direction: column;
  align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

.RestClckVar{
  font-size: 16px;
  color: #696969;
}

.RestClckAdd{
  font-size: 16px;
  color: #9c9c9c;
}

.RestClckTime{
  font-size: 14px;
  color: #9c9c9c;
}

.RestClckbtn{
margin-left: 1px;
}

.RestClckMenu li{
  list-style-type: none;
  display: inline;
  font-family: 'Montserrat', sans-serif;
  font-size: 18px;
  color: #696969;
  cursor: pointer;
  margin-right: 50px;
  padding: 17px 7px 17px 7px;

}

