/* To add responsiveness */
@media(max-width:998px){
    body{
        font-size: 55%;
    }
    .search, .lists, .filter,.delTakeout{
      display: none;
    }
    .user{
      display: none;
    }
}

@media(max-width:768px){
    body{
        font-size: 45%;
    }
    .search, .lists, .filter,.delTakeout{
      display: none;
    }
    .user{
      display: none;
    }
}
